      });
      for (let key of map.keys()) {
        // console.log(key + "." + map.get(key));
        str = str + key + "^" + map.get(key) + "\n";
      }
      this.stage[Sindex].key_orders.extra_field[index].field_content= str;
      // console.log(str);
    },
    // 清除时间最大值最小值  
    fillRemoveUnit(describe,index) {   
      if(describe == "其他描述") {      
        this.stage[index].currentMin = 0;
        this.stage[index].currentMax = 0;       
        this.stage[index].currentUnit = ""
      }else {
        this.stage[index].extra_description = ""
      }
    },
    replace2(index) {
      let map = new Map();
      let text = this.stage[index].key_orders.long_term_orders
        .replace(/[\r\n]/g, ",")
        .replace(/\s/g, "")
        .replace(/\□/g, "")
        .split(",");
      let str = "";
      // 过滤空格
      text = text.filter(item => {
        return text != "";
      });
      text.forEach((item, index) => {
        const pointIndex = item.indexOf("^");
        // console.log(pointIndex);

        // 防止截取药品用量的小数点
        if (item.slice(0, pointIndex).length <= 2) {
          item = item.substring(pointIndex + 1);
          // console.log(item);
        }
        // console.log(item);
        map.set(index + 1, item);
      });
      for (let key of map.keys()) {
        // console.log(key + "." + map.get(key));
        str = str + key + "^" + map.get(key) + "\n";
      }
      this.stage[index].key_orders.long_term_orders = str;
      // console.log(this.stage[index].key_orders.long_term_orders);
    },
    // 重点医嘱---临时医嘱
    replace3(index) {
      let map = new Map();
      let text = this.stage[index].key_orders.temporary_orders
        .replace(/[\r\n]/g, ",")
        .replace(/\s/g, "")
        .replace(/\□/g, "")
        .split(",");
      let str = "";
      text.forEach((item, index) => {
        const pointIndex = item.indexOf("^");
        // console.log(pointIndex);

        // 防止截取药品用量的小数点
        if (item.slice(0, pointIndex).length <= 2) {
          item = item.substring(pointIndex + 1);
          // console.log(item);
        }
        // console.log(item);
        map.set(index + 1, item);
      });
      for (let key of map.keys()) {
        // console.log(key + "." + map.get(key));
        str = str + key + "^" + map.get(key) + "\n";
      }
      this.stage[index].key_orders.temporary_orders = str;
      // console.log(str);
    },
    // 重点医嘱---出院医嘱
    replace4(index) {
      let map = new Map();
      let text = this.stage[index].key_orders.discharge_orders
        .replace(/[\r\n]/g, ",")
        .replace(/\s/g, "")
        .replace(/\□/g, "")
        .split(",");
      let str = "";
      text.forEach((item, index) => {
        // -----------
        const pointIndex = item.indexOf("^");
        // console.log(pointIndex);

        // 防止截取药品用量的小数点
        if (item.slice(0, pointIndex).length <= 2) {
          item = item.substring(pointIndex + 1);
          // console.log(item);
        }
        // console.log(item);
        map.set(index + 1, item);
      });
      // console.log(map);
      for (let key of map.keys()) {
        // console.log(key + "." + map.get(key));
        str = str + key + "^" + map.get(key) + "\n";
      }
      this.stage[index].key_orders.discharge_orders = str;
      // console.log(str);
    },
    // 重点医嘱---重点注意事项
    replace5(index) {
      let map = new Map();
      let text = this.stage[index].key_orders.notification
        .replace(/[\r\n]/g, ",")
        .replace(/\s/g, "")
        .replace(/\□/g, "")
        .split(",");
      let str = "";
      text.forEach((item, index) => {
        // -----------
        const pointIndex = item.indexOf("^");
        // console.log(pointIndex);

        // 防止截取药品用量的小数点
        if (item.slice(0, pointIndex).length <= 2) {
          item = item.substring(pointIndex + 1);
          // console.log(item);
        }
        // console.log(item);
        map.set(index + 1, item);
      });
      // console.log(map);
      for (let key of map.keys()) {
        // console.log(key + "." + map.get(key));
        str = str + key + "^" + map.get(key) + "\n";
      }
      this.stage[index].key_orders.notification = str;
      // console.log(str);
    },
    // 主要护理工作
    replace6(index) {
      let map = new Map();
      let text = this.stage[index].major_nursing_work
        .replace(/[\r\n]/g, ",")
        .replace(/\s/g, "")
        .replace(/\□/g, "")
        .split(",");
      let str = "";
      text.forEach((item, index) => {
        // -----------
        const pointIndex = item.indexOf("^");
        // console.log(pointIndex);

        // 防止截取药品用量的小数点
        if (item.slice(0, pointIndex).length <= 2) {
          item = item.substring(pointIndex + 1);
          // console.log(item);
        }
        // console.log(item);
        map.set(index + 1, item);
      });
      // console.log(map);
      for (let key of map.keys()) {
        // console.log(key + "." + map.get(key));
        str = str + key + "^" + map.get(key) + "\n";
      }
      this.stage[index].major_nursing_work = str;
      // console.log(str);
    },
    // 病情变异记录
    replace7(index) {
      let map = new Map();
      let text = this.stage[index].reason
        .replace(/[\r\n]/g, ",")
        .replace(/\s/g, "")
        .replace(/\□/g, "")
        .split(",");
      let str = "";
      text.forEach((item, index) => {
        // -----------
        const pointIndex = item.indexOf("^");
        // console.log(pointIndex);

        // 防止截取药品用量的小数点
        if (item.slice(0, pointIndex).length <= 2) {
          item = item.substring(pointIndex + 1);
          // console.log(item);
        }
        // console.log(item);
        map.set(index + 1, item);
      });
      // console.log(map);
      for (let key of map.keys()) {
        // console.log(key + "." + map.get(key));
        str = str + key + "^" + map.get(key) + "\n";
      }
      this.stage[index].reason = str;
      // console.log(str);
    },
    select() {
      let cpId = this.cp_id;
      if (cpId == "") {
        this.$message({
          showClose: true,
          message: "请输入正确的ID号",
          type: "error"
        });
      } else {
        this.$axios
          .get("http://192.168.50.78:8002/getExamFormDataById", {
            params: {
              cp_id: this.cp_id
            }
          })
          .then(res => {
            // console.log(res);
            this.status = res.data.status;
            if (this.status == 404) {
              this.$message({
                showClose: true,
                message: "请输入正确的ID号",
                type: "error"
              });
              this.form_name = "";
              this.severity_level = "0";
              this.suitable_subject_disc = "";
              this.standard_entry_days = "天";
              this.timeMax = "0";
              this.timeMin = "0";
              this.stage = [
                {
                  currentMin: "0",
                  currentMax: "0",
                  currentUnit: "天",
                  isOperation: "0",
                  options2: [{
                      value: '天',
                      label: '天'
                    }, {
                      value: '时',
                      label: '时'
                    }, {
                      value: '周',
                      label: '周'
                    }, {
                      value: '月',
                      label: '月'
                    }],
                      // 描述前缀
                      prefix_description: [{
                      value: '住院',
                      label: '住院'
                    }, {
                      value: '出院前',
                      label: '出院前'
                    }, {
                      value: '出院后',
                      label: '出院后'
                    }, {
                      value: '其他描述',
                      label: '其他描述'
                    }],
                  fill_prefix_description:"住院",
                   // 手术 偏移量
                  operationNum: "",
                  primary_diagnosi: [],
                  key_orders: {
                    long_term_orders: [],
                    temporary_orders: [],
                    discharge_orders: [],
                    notification: []
                  },
                  major_nursing_work: [],
                  disease_variation_status: "0",
                  reason: []
                }
              ];
              return;
            } else {
              // console.log(res);             
              this.form_name = res.data.form_name;
              this.severity_level = res.data.severity_level;
              this.suitable_subject_disc = res.data.suitable_subject_disc;
              this.standard_entry_days = res.data.standard_entry_days;
              this.timeMax = res.data.timeMax;
              this.timeMin = res.data.timeMin;
              this.stage = res.data.stage;
              // console.log(res.data.stage);
              res.data.stage.forEach((item, index) => {               
                this.stage[index].primary_diagnosi = this.repeat(
                  item.primary_diagnosi
                );
                this.stage[index].major_nursing_work = this.repeat(
                  item.major_nursing_work
                );
                this.stage[index].reason = this.repeat(item.reason);
                this.stage[index].key_orders.discharge_orders = this.repeat(
                  item.key_orders.discharge_orders
                );
                this.stage[index].key_orders.long_term_orders = this.repeat(
                  item.key_orders.long_term_orders
                );
                this.stage[index].key_orders.notification = this.repeat(
                  item.key_orders.notification
                );
                this.stage[index].key_orders.temporary_orders = this.repeat(
                  item.key_orders.temporary_orders
                );
                // console.log(this.stage[index].key_orders.extra_field[index].field_content);                            
                let extraField = item.key_orders.extra_field;
                extraField.forEach((item,index) => {
                let text = this.repeat(item.field_content);
                // console.log(text);
                // console.log(item.field_content);
                item.field_content = text;
                });
              });
            }
          })
          .catch(err => {
            console.log(err);
          });
      }
    },
    repeat(arr) {
      let str = "";
      // console.log(arr);
      if (arr != "") {
        if (arr.length != 0) {
          arr.forEach((item, index) => {
            // console.log(item);
            // item 为空就不执行
            if (item != "") {
              if (index == 0) {
                // 数组里只有一项的时候 就只返回一个
                str = index + 1 + "^" + item;
                // console.log(str);
              } else {
                str = str + "\n" + (index + 1) + "^" + item;
                // console.log(str);
              }
            }
          });
          return str;
        }
      } else {
        return str;
      }
    },
    FilltimeUnit() {
      this.stage.forEach((item, index) => {
        // console.log(item);
        // console.log(item.currentUnit);
        item.currentUnit = this.standard_entry_days;
      });
      // this.stageItem.currentUnit = this.standard_entry_days;
    },
    fillbtn() {
       var returnInfo = 0;
      this.stage.forEach((item,index) =>{
        if(item.isOperation == 1 && item.operationNum == '') {
          returnInfo = 1;
        }
      });     
      if(returnInfo == 1) {
        this.$message({
          showClose: false,
          message: '请将手术日偏移数补充完整！',
          type: 'error',
          duration: 1000
        });
        return false;
      }
      var stagg = JSON.stringify(this.stage);
      let formName = this.form_name;
      let cpId = this.cp_id;
      if (formName == "" || cpId == "") {
        this.$alert("务必填写名字以及ID号", "请注意", {
          confirmButtonText: "确定",
          callback: action => {
            this.$message({
              type: "info",
              message: "已取消提交"
            });
          }
        });
      } else {
        this.$confirm("请确认是否提交信息？", "确认信息", {
          distinguishCancelAndClose: true,
          confirmButtonText: "确定提交",
          cancelButtonText: "放弃提交"
        })
          .then(() => {
            this.$axios
              .get("http://192.168.50.78:8002/addOriginalNewForm", {
                params: {
                  cp_id: this.cp_id,
                  form_manager: this.form_manager,
                  form_name: this.form_name,
                  suitable_subject_disc: this.suitable_subject_disc,
                  standard_entry_days: this.standard_entry_days,
                  timeMin: this.timeMin,
                  timeMax: this.timeMax,
                  severity_level: this.severity_level,
                  stage: stagg
                }
              })
              .then(res => {
                this.$message({
                  message: "数据提交成功",
                  type: "success"
                });
                window.location.reload();
                // console.log("成功了");
              })
              .catch(err => {
                this.$message.error("数据提交失败~");
                console.log(err);
              });
          })
          .catch(action => {
            this.$message("数据未提交");
          });
      }
      // console.log(this.stage);
    }
  }
};
</script>

<style scoped>
.fill-el-header {
  background-color: #ffffff;
  min-height: 180px;
  color: #333;
  text-align: center;
}
.fill-el-main {
  overflow: auto;
  background-color: #e9eef3;
  color: #333;
  text-align: center;
}
.fill-el-row {
  margin-bottom: 20px;
}
.fill-el-col {
  border-radius: 4px;
}
.fill-bg-purple-dark {
  border-radius: 4px;
  text-align: left;
  background: #99a9bf;
}
.fill-input1 {
  width: 90%;
  height: 42px;
  margin-left: 22px;
  border: none;
  outline: none;
  border-bottom: 1px solid #9e9e9e;
  text-align: center;
}
/* input[type="text"]:focus {
  border-bottom: 1px solid #0d47a1;
  box-shadow: 0 1px 0 0 #0d47a1;
} */
.fill-container {
  padding: 10px 0px;
  width: 100%;
  height: 100%;
}
.fill-scene-container {
  width: 33.333%;
  border-radius: 2px;
  background-color: #fff;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
    0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
  float: left;
  box-sizing: border-box;
  padding: 10px 0px;
  min-height: 100px;
}
.fill-table {
  width: 100%;
  min-height: 100%;
  border: 1px solid red;
  display: flex;
  box-sizing: border-box;
}
.fill-table-sun {
  /* width: 30px; */
  writing-mode: vertical-lr;
  letter-spacing: 5px;
  font-size: 20px;
  padding: 0px 10px;
  display: inline-block;
  border-right: 1px solid green;
}
.fill-textarea {
  resize: none;
  width: 98%;
  height: 100%;
  border: none;
  outline: none;
}
.fill-el-main::-webkit-scrollbar {
  width: 3px;
}
.fill-el-main::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.2);
  border-radius: 1px;
}

.fill-el-main::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.4);
}
</style>



